<template>
  <div id="user">
      <div class="user-Myinfo">
          <div class="myinfo-div">
              <div class="myImg">
                  <img src="../../assets/images/user.jpg" alt="">
              </div>
          </div>
          <div class="myinfo-div">
              <div class="nackname">
                    <div>{{userItem.username}}</div>
                    <div><van-icon name="gem" />黑金会员</div>
              </div>
          </div>
          <div class="myinfo-div">
              <van-circle
                :stroke-width="80"
                v-model="currentRate"
                :rate="88"
                speed="100"
                :color="gradientColor"
                text="88%"
                />
          </div>
      </div>
      <van-notice-bar left-icon="volume-o" :scrollable="false">
        <van-swipe vertical class="notice-swipe" :autoplay="3000" :show-indicators="false">
            <van-swipe-item v-for="(item, index) in swipeList" :key="index">{{item}}</van-swipe-item>
        </van-swipe>
    </van-notice-bar>
    <div class="mycont">
        <ul>
            <li>
                <div><van-icon name="location-o" /></div>
                <div>签到</div>
            </li>
            <li>
                <div><van-icon name="todo-list-o" /></div>
                <div>日历</div>
            </li>
            <li>
                <div><van-icon name="balance-list-o" /></div>
                <div>账单</div>
            </li>
            <li>
                <div><van-icon name="apps-o" /></div>
                <div>任务</div>
            </li>
        </ul>
    </div>
    <van-swipe-cell>
        <van-cell :border="false" title="支付" value=">" />
        <template #right>
            <van-button square type="danger" text="查看" />
            <van-button square type="primary" @click="tips" text="收藏" />
        </template>
    </van-swipe-cell>
    <van-swipe-cell>
        <van-cell :border="false" title="收藏" value=">" />
        <template #right>
            <van-button square type="danger" text="查看" />
            <van-button square type="primary" @click="tips" text="收藏" />
        </template>
    </van-swipe-cell>
    <van-swipe-cell>
        <van-cell :border="false" title="朋友圈" value=">" />
        <template #right>
            <van-button square type="danger" text="查看" />
            <van-button square type="primary" @click="tips" text="收藏" />
        </template>
    </van-swipe-cell>
    <van-swipe-cell>
        <van-cell :border="false" title="卡包" value=">" />
        <template #right>
            <van-button square type="danger" text="查看" />
            <van-button square type="primary" @click="tips" text="收藏" />
        </template>
    </van-swipe-cell>
    <van-swipe-cell>
        <van-cell :border="false" title="表情" value=">" />
        <template #right>
            <van-button square type="danger" text="查看" />
            <van-button square type="primary" @click="tips" text="收藏" />
        </template>
    </van-swipe-cell>
    <van-swipe-cell>
        <van-cell :border="false" title="设置" value=">" />
        <template #right>
            <van-button square type="danger" text="查看" />
            <van-button square type="primary" @click="tips" text="收藏" />
        </template>
    </van-swipe-cell>
    <div style="margin: 16px;" class="goLogin">
        <van-button round block type="info" @click="gologin" native-type="submit">退 出</van-button>
    </div>
  </div>
</template>

<script>
import { Notify } from 'vant';
export default {
    components: {
        [Notify.Component.name]: Notify.Component,
    },
    data() {
        return {
            userItem: JSON.parse(localStorage.getItem("userItem"))||{},
            currentRate: 0,
            swipeList: ['2021年挣大钱💰','多学习，多看书📖','哈哈哈哈哈哈😄'],
            gradientColor: {
                '0%': '#3fecff',
                '100%': '#6149f6',
            },
        }
    },methods: {
        tips() {
            Notify({ type: 'primary', message: '收藏成功!' });
        },
        gologin() {
            localStorage.removeItem('userItem');
            this.$router.push('/login')
        }
    }
}
</script>

<style>
.notice-swipe {
    height: 40px;
    line-height: 40px;
  }
</style>


