<template>
  <div id="login">
      <div class="login-form">
        <van-tabs v-model="formaActive" @click="form.username='';form.password='';">
            <van-tab title="登陆"></van-tab>
            <van-tab title="注册"></van-tab>
        </van-tabs>
        <van-form @submit="goHome">
            <template v-if="formaActive==0">
                <van-field
                    v-model.trim="form.username"
                    label="账号："
                    placeholder="请输入"
                />
                <van-field
                    v-model.trim="form.password"
                    type="password"
                    label="密码："
                    placeholder="请输入"
                />
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit">登陆</van-button>
                </div>
            </template>
            <template v-else>
                <van-field
                    v-model.trim="form.username"
                    label="账号："
                    placeholder="请输入"
                />
                <van-field
                    v-model.trim="form.password"
                    type="password"
                    label="密码："
                    placeholder="请输入"
                />
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit">注册</van-button>
                </div>
            </template>
        </van-form>
      </div>
  </div>
</template>

<script>
import { Notify } from 'vant';
export default {
    components: {
        [Notify.Component.name]: Notify.Component,
    },
    data(){
        return {
            formaActive: 0,
            form: {
                username:'',
                password: ''
            }
        }
    },
    methods: {
        goHome() {
            let _this = this;
            if(this.form.username&&this.form.password) {
                if(this.form.username.length<2||this.form.password.length<2) {
                    Notify('账号/密码不可以少于2位');
                    return;
                }
            } else {
                Notify('请输入账号/密码');
                return;
            };
            if(this.formaActive==0) {
                let userList = localStorage.getItem('userList')?JSON.parse(localStorage.getItem('userList')):[];
                let user = {};
                userList.forEach((item)=>{
                    if(item.username==_this.form.username&&item.password==_this.form.password) {
                        user = item;
                    }
                },)
                setTimeout(() => {
                    if(user.username&&user.password) {
                        localStorage.setItem('userItem',JSON.stringify({
                            username: _this.form.username, 
                            password: _this.form.password
                        }))
                        _this.$router.push('/home')
                    } else {
                        Notify('该用户未注册,请前往注册!');
                    }
                }, 300);
            } else {
                let userList = localStorage.getItem('userList')?JSON.parse(localStorage.getItem('userList')):[];
                let user = {};
                userList.forEach((item)=>{
                    if(item.username==_this.form.username&&item.password==_this.form.password) {
                        user = item;
                    }
                },)
                setTimeout(() => {
                    if(user.username&&user.password) {
                        Notify('您已注册,请前往登陆!');
                    } else {
                        userList.push({
                           username: _this.form.username, 
                           password: _this.form.password 
                        })
                        Notify({ type: 'primary', message: '注册成功,请前往登陆!' });
                        localStorage.setItem('userList',JSON.stringify(userList))
                    }
                }, 300);
            }
        }
    }
}
</script>

<style>

</style>


