<template>
  <div>
      <!-- 总容器 -->
      <div class="home-view">
          <router-view/>
      </div>
      <van-tabbar v-model="active" @change="onChange">
        <van-tabbar-item icon="smile-comment-o" badge="58">消息</van-tabbar-item>
        <van-tabbar-item icon="friends-o" badge="5">朋友</van-tabbar-item>
        <van-tabbar-item icon="more-o">发现</van-tabbar-item>
        <van-tabbar-item icon="user-o">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { Notify } from 'vant';
export default {
    components: {
        [Notify.Component.name]: Notify.Component,
    },
    data() {
        return {
            active: 0
        }
    },
    methods: {
        onChange(index) {
            switch (index) {
                case 0:
                    this.$router.push('/home');
                    break;
                case 3:
                    this.$router.push('/user');
                    break;
            
                default:
                    Notify('功能未开发')
                    break;
            }
            console.log(index)
        }
    }
}
</script>

<style>
.home-view {
    background-color: #d0d0d0;
    /* height: calc(100vh - 50px); */
    overflow-y: auto;
    position: fixed;
    top: 0;
    bottom: 50px;
    right: 0;
    left: 0;
}
</style>


